import React, {PureComponent, Fragment} from 'react';
import Switch from '@eui/ic-switch';
import Markdown from 'ic-markdown';
import switchMD from './switch.md';

function onChange(e, checked) {
    console.log(`switch to ${checked}`);
}

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section>
                    <Switch onChange={onChange}/> <br />最简单的用法，只指定变化的回调函数
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="已开启" uncheckedLabel="已关闭" /> <br />带有文字的开关
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} disabled={true} /> <br />初始选中的失效状态
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="已开启" uncheckedLabel="已关闭" disabled /> <br />初始未选中的失效状态
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="已开启" uncheckedLabel="已关闭" size="small" /> <br />小号开关
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={true} checkedLabel="已开启" uncheckedLabel="已关闭" loading /> <br />加载状态（开）
                </section>
                <br/>
                <section>
                    <Switch onChange={onChange} defaultChecked={false} checkedLabel="已开启" uncheckedLabel="已关闭" loading /> <br />加载状态（关）
                </section>
                <section>
                    <Markdown>{switchMD}</Markdown>
                </section>
            </Fragment>
        );
    }
}




