import React, {PureComponent} from 'react'
import Simple,{CustomRules} from './Simple'
import Markdown from 'ic-markdown'
import simple from './simple.md'
import customRules from './customRules.md'

export default () => {
    return (
        <div>
            <section>简单表单</section>
            <section>
                <Simple />
            </section>
            <section>
                <Markdown>{simple}</Markdown>
            </section>
            <section>自定义规则和远程校验表单</section>
            <section>
                <CustomRules />
            </section>
            <section>
                <Markdown>{customRules}</Markdown>
            </section>
        </div>
    );
}



