import React, {PureComponent} from 'react'
import Simple,{RealTime} from './Simple'
import Markdown from 'ic-markdown'
import simple from './simple.md'
import realTime from './realTime.md'

export default () => {
    return (
        <div>
            <section>普通submit按钮</section>
            <section>
                <Simple />
            </section>
            <section>
                <Markdown>{simple}</Markdown>
            </section>
            <section>实时submit按钮</section>
            <section>
                <RealTime />
            </section>
            <section>
                <Markdown>{realTime}</Markdown>
            </section>
        </div>
    );
}



