import React, {PureComponent, Fragment} from 'react';
import Scroll from '@eui/ic-scroll';
import Markdown from 'ic-markdown';
import defaultMD from './default.md';
import lightMD from './light.md';
import lighterMD from './lighter.md';
import noneMD from './none.md';
import configMD from './config.md';
import './index.css';

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section>滚动条</section>
                <section>
                    <p>type默认为default</p>
                    <Scroll>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{defaultMD}</Markdown>
                </section>
                <section>
                    <p>type默认为light</p>
                    <Scroll type='light'>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{lightMD}</Markdown>
                </section>
                <section>
                    <p>type默认为lighter</p>
                    <Scroll type='lighter'>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{lighterMD}</Markdown>
                </section>
                <section>
                    <p>内容高度小于容器高度时，不出现滚动条</p>
                    <Scroll>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{noneMD}</Markdown>
                </section>
                <section>
                    <p>根据better-scroll的API配置config</p>
                    <Scroll config={{startY: -30}}>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                        <div>滚动条。。。</div>
                    </Scroll>
                </section>
                <section>
                    <Markdown>{configMD}</Markdown>
                </section>
            </Fragment>
        );
    }
}




