module.exports = "```js\nimport Switch from '@eui/ic-switch';\n\nfunction onChange(e, checked) {\n    console.log(`switch to ${checked}`);\n}\n\n<Switch onChange={onChange}/>\n<Switch onChange={onChange} defaultChecked={false} checkedLabel=\"已开启\" uncheckedLabel=\"已关闭\" />\n<Switch onChange={onChange} disabled={true} />\n<Switch onChange={onChange} defaultChecked={false} checkedLabel=\"已开启\" uncheckedLabel=\"已关闭\" disabled />\n<Switch onChange={onChange} defaultChecked={false} size=\"small\" /> <br />小号开关\n<Switch onChange={onChange} defaultChecked={true} loading /> <br />加载状态（开）\n<Switch onChange={onChange} defaultChecked={false} loading /> <br />加载状态（关）\n```\n"

