module.exports = "## ic-switch\n\n描述：开关切换\n\n创建人：suzhen.han\n\n创建时间：2018年05月02日 11:01\n\n仓库地址：http://gitlab.zhinanzhen.wiki/labs/hr-components\n\n-----------\n\n需要表示开关状态/两种状态之间的切换时可使用开关切换组件，其中onChange为切换后需要执行的回调函数，参数是当前的选中状态（布尔值）。具体参数说明可代码示例参见下文。\n\n### 参数描述\n\n| 参数名 | 类型  | 是否必须 | 说明  | 默认值 |\n| --- | --- | ---- | --- | --- |\n| defaultChecked | boolean | 否 | 初始是否开启 | true |\n| checked | boolean | 否 | 是否开启 | true |\n| checkedLabel | string | 否 | 开启时的说明,正常尺寸非加载状态时有效 | 无 |\n| uncheckedLabel | string | 否 | 关闭时的说明,正常尺寸非加载状态时有效 | 无 |\n| disabled | boolean | 否 | 是否禁用 | false |\n| loading | boolean | 否 | 加载状态 | false |\n| size | string | 否 | 大小，提供`default` `small`两种大小 | default |\n| onChange | Function(event, checked:Boolean) | 是 | 变化时的回调函数 | () => {} |\n\n-----\n\n### Blog:\n\n- 2018年05月02日 11:01：**suzhen.han** 创建组件\n"

