module.exports = "## @eui/ic-popup\n\n描述：对话框组件\n\n创建人：jingfan\n\n创建时间：2018年05月24日 14:45\n\n仓库地址: git@gitlab.zhinanzhen.wiki:labs/hr-components.git\n\n-----------\n\n### 参数描述\n\n| 参数名 | 类型 | 是否必须 | 说明 | 默认值 |\n| --- | --- | ---- | --- | --- |\n|afterClose|function|      |Modal 完全关闭后的回调|-|\n|cancelText|string|      |取消按钮文字|取消|\n|closable|boolean|      |是否显关闭按钮|true|\n|footer|string/ReactNode|      |底部内容。设为null时可以完全隐藏|确定取消按钮|\n|okText|string|      |确认按钮文字|确定|\n|onOk|function|      |点击确定时的回调|-|\n|onCancel|function|      |点击关闭、取消时的回调|-|\n|size|string/number|   |对话框宽度,normal为500px,small为360px,数字时为自定义|normal|\n|type|string|      |设置body内的内置图标,可选值info,warning,success|-|\n|visible|boolean|      |对话框是否可见|false|\n|wrapClassName|string|      |对话框外层容器的类名|-|\n\n### API描述\n\n| 名称  | 参数列表 | 返回值 | 功能说明 |\n| --- | ---- | --- | ---- |\n|normal|config|{destroy: fn}|创建一个对话框，并返回销毁方法|\n|info|config|{destroy: fn}|创建一个type为info的对话框，并返回销毁方法|\n|success|config|{destroy: fn}|创建一个type为success的对话框，并返回销毁方法|\n|warning|config|{destroy: fn}|创建一个type为warning的对话框，并返回销毁方法|\n\n`config`\n一个包含配置参数的对象。\n其中footer有别于常规调用方式。\n\n| 参数名 | 类型 | 是否必须 | 说明 | 默认值 |\n| --- | --- | ---- | --- | --- |\n|footer|Function(close):ReactNode|      |底部内容。设为null时可以完全隐藏。接受一个参数close(关闭对话框的方法)，返回ReactNode的函数。|-|\n-----\n\n### Blog:\n\n- 2018年05月24日 14:45：**jingfan** 创建组件\n"

