module.exports = "```js\nimport Button from '@eui/ic-button'\nimport toast from '@eui/ic-toast'\n<Button onClick={() => { toast.message({ content: `长长的内容`, duration: 5 }) }}>messgae</Button>\n<Button onClick={() => { toast.success({ content: `长长的内容`, duration: 5 }) }}>success</Button>\n<Button onClick={() => { toast.info({ content: `长长的内容`, duration: 5 }) }}>info</Button>\n<Button onClick={() => { toast.error({ content: `长长的内容`, duration: 5 }) }}>error</Button>\n```\n"

