module.exports = "## ic-toast\n\n描述：eui toast组件\n\n创建人：jingfan\n\n创建时间：2018年05月30日 15:44\n\n仓库地址: git@gitlab.zhinanzhen.wiki:labs/hr-components.git\n\n[gitlab](http://gitlab.zhinanzhen.wiki/labs/hr-components)\n\n-----------\n\n### API描述\n提供了一些静态方法\ntoast.success(content, [duration], onClose)\ntoast.error(content, [duration], onClose)\ntoast.info(content, [duration], onClose)\ntoast.warning(content, [duration], onClose)\n每个方法都返回{destroy:Function}\b\n\n| 参数名 | 类型 | 说明 | 默认值 |\n| --- | --- | ---- | --- |\n|content| string/ReactNode |提示内容|-|\n|duration| number | 自动关闭的延时，单位秒。设为 0 时不自动关闭。 |  3 |\n|onClose|Function|关闭时触发的回调函数|-|\n|destroy|Function|调用后，可以关闭提示框|-|\n\n-----------\n\n还提供了全局配置的方法\ntoast.config({[duration:Number],[top:number],[maxCount:number]})\n\n| 参数名 | 类型 | 说明 | 默认值 |\n| --- | --- | ---- | --- |\n|top| number |距离页面顶部的距离，单位px| 80 |\n|duration| number | 设置默认延时关闭时间。设为0为不自动关闭 |  3 |\n|maxCount| number | 提示同时存在的最大值。超过该值，先出现的会被关闭 |-|\n-----------\n\n"

