module.exports = "和 browserHistory 配合     \n\n和 react-router 一起使用时，默认生成的 url 路径是带有 # 的，如果和 browserHistory 一起使用的话，你可以使用 render 属性定义面包屑链接。\n```js\nimport { Link } from 'react-router-dom';\nimport IcBreadcrumb from '@eui/ic-breadcrumb'\nconst routes = [{\n  path: '/index',\n  breadcrumbName: '首页'\n}, {\n  path: 'first',\n  breadcrumbName: '一级面包屑'\n}, {\n  path: 'second',\n  breadcrumbName: '当前页面'\n}];\n\nfunction itemRender(route, params, routes, paths) {\n  const last = routes.indexOf(route) === routes.length - 1;\n  return last ? <span>{route.breadcrumbName}</span> : <Link to={'/'+paths.join('/')}>{route.breadcrumbName}</Link>;\n}\n<IcBreadcrumb routes={routes} render={itemRender} />\n```"

