module.exports = "```js\nimport React, {PureComponent} from 'react'\nimport Form from 'ic-form'\nimport {Input} from '@eui/ic-field-decorator'\nimport SubmitButton from '@eui/ic-submit-button'\n\nclass CustomRules extends PureComponent {\n    handlerSubmit = (data) => {\n        console.log(\"提交表单\", data);\n    };\n\n    render() {\n        return (\n            <Form onSubmit={this.handlerSubmit} rules={{\n                NUM: (value) => {\n                    return {\n                        result: !Number.isNaN(Number(value)),\n                        errMsg: '%s必须是数字'\n                    };\n                },\n                IS_EXIST: (value) => {\n                    return new Promise((resolve) => {\n                        const phoneSet = ['18000000000', '18000000001', '18000000002'];\n                        setTimeout(() => resolve({\n                            result: value && phoneSet.indexOf(value) > -1,\n                            errMsg: '%s不存在'\n                        }), 1000);\n                    });\n                }\n            }}>\n                <Input name=\"name\" label=\"用户名\" rule=\"REQ NAME LEN-2-10\"/>\n                <Input type=\"password\" name=\"pwd\" label=\"密码\" rule=\"REQ NUM LEN-6-12\"/>\n                <Input name=\"tel\" label=\"电话\" rule=\"REQ TEL IS_EXIST\"/>\n                <SubmitButton>保存</SubmitButton>\n            </Form>\n        );\n    }\n}\n```\n"

