module.exports = "## ic-field-decorator\n\n描述：表单字段修饰器，用来将表单组件的错误更加美观地展示给用户\n\n创建人：admin\n\n创建时间：2018年06月28日 11:15\n\n仓库地址: git@gitlab.zhinanzhen.wiki:labs/hr-components.git\n-----------\n\n### 概念\n\n在表单组件的设计中，因为校验逻辑应该是固定不变的，校验表现应该是根据UI，使用场景不同而经常变化的；所以我们需要将校验逻辑和校验表现分离，使校验逻辑统一而表现多样。field-decorator就是一个字段的修饰器，它决定了字段输入组件外层的表现形式。根据目前UI标准我们实现了一个默认修饰器。\n\nfield-decorator和field一样是一个高阶组件，它拦截了由field传过来的校验结果等信息，它接受一个输入组件，如Input，Select等，返回一个带有验证信息表现的组件。\n\n为了方面使用，该模块默认导出一个加了field高阶组件修饰，可以直接使用。同时也返回自身，导入方法如下\n\n```js\nimport {fieldDecorator} from '@eui/ic-field-decorator'\n```\n\n更多API文档请参考 [npm.zhinanzhen.ai](http://npm.zhinanzhen.ai/) ic-form 文档\n\n### 参数描述\n\n| 参数名 | 类型 | 是否必须 | 说明 | 默认值 |\n| --- | --- | ---- | --- | --- |\n|   WrappedComponent  |  ReactNode   |  是    |   输入组件，如Input，Select等  |   -  |\n\n-----\n\n### Blog:\n\n- 2018年06月28日 11:15：**admin** 创建组件\n"

