import React, { PureComponent,Fragment} from 'react'
import IcRangeInput from '@eui/ic-range-input'
import Markdown from 'ic-markdown'
import range from './range.md'

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section className="single-icon">区间值输入框</section>
                <section>
                    <IcRangeInput  placeholder = {["0"]} width="300" unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput  placeholder = {["0","1"]} width={300} unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput formatter={[(value)=>{return value.replace(/[^\d]/g,'')}]} unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput formatter={[(value)=>{return value.replace(/[^\d]/g,'')},(value)=>{return value.replace(/[^\d]/g,'')}]} unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput formatter="integer" unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput formatter="natural" unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput formatter="float" unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput defaultValue={["0"]} unit="元"></IcRangeInput>
                </section>
                <section>
                    <IcRangeInput defaultValue={["0","1"]} unit="元"></IcRangeInput>
                </section>
                <section>
                    <Markdown>
                        {range}
                    </Markdown>
                </section>
            </Fragment>
        );
    }
}



