import React, { Component } from 'react'
import Button from '@eui/ic-button'
import Markdown from 'ic-markdown'
import code from './index.md'
import './style.css'
class App extends Component {
  render() {
    return (
      <section className='section-ic-btn'>
        <section>
          <Button icon='search_normal' size='large' type='primary'> 大按钮 </Button>
          <Button size='large' type='primary'>大按钮</Button>
          <Button size='large' type='primary' loading >大按钮</Button>
          <Button size='large'>大按钮</Button>
          <Button size='large'>六个字大按钮</Button>
          <Button size='large' loading >大按钮</Button>
          <Button size='large' icon='search_normal' disabled >大按钮</Button>
        </section>
        <section>
          <Button type='primary' icon='search_normal'>按钮</Button>
          <Button type='primary'>正常按钮</Button>
          <Button type='primary' loading>正常按钮</Button>
          <Button>正常按钮</Button>
          <Button>五个字按钮</Button>
          <Button loading>正常按钮</Button>
          <Button disabled>正常按钮</Button>
        </section>
        <section>
          <Button type='primary' size='small' icon='search_normal'>按钮</Button>
          <Button type='primary' size='small'>辅助按钮</Button>
          <Button type='primary' size='small' loading>辅助按钮</Button>
          <Button size='small'>辅助按钮</Button>
          <Button size='small'>五个字按钮</Button>
          <Button size='small' loading>辅助按钮</Button>
          <Button size='small' disabled>辅助按钮</Button>
        </section>
        <Markdown>{code}</Markdown>
      </section>
    )
  }
}

export default App



