import React, {Fragment} from 'react'
import IcEmpty from "@eui/ic-empty";
import img from './timg.jpeg'
import Markdown from 'ic-markdown'
import custom from './custom.md'
import other from './other.md'
import './index.scss'
const Search = IcEmpty.Search
const List = IcEmpty.List
const Child = IcEmpty.Child
const Other = IcEmpty.Other
const Record = IcEmpty.Record

const Recommend = IcEmpty.Recommend
const NotGet = IcEmpty.NotGet
const Notify = IcEmpty.Notify
const Award = IcEmpty.Award

const Interview = IcEmpty.Interview
class Empty extends React.PureComponent {

    render() {
        return (<Fragment>
            <IcEmpty src={img} size='small' text='自定义Empty' />
            <Markdown>{custom}</Markdown>
            <Search />
            <List />
            <Child />
            <Other />
            <Recommend />
            <NotGet />
            <Notify />
            <Award />
            <Interview />
           <Markdown>{other}</Markdown>
        </Fragment>)
    }
}


export default Empty



