import React from 'react';
import Checkbox from '@eui/ic-checkbox';

function onChange(e) {
    console.log(`checked = ${e.target.checked}`);
}

export default function App() {
    return (
        <Checkbox onChange={onChange}>Checkbox</Checkbox>
    );
}


