import React, {PureComponent, Fragment} from 'react'
import Markdown from 'ic-markdown'
import IcTextarea from '@eui/ic-textarea'
import textareaMd from './textarea.md'

export default class App extends PureComponent {
    handleValue =(value) =>{
        console.log('我是文本框的内容：',value);
    }
    render() {
        return (
            <Fragment>
                <section>
                    <IcTextarea  placeholder="小姐姐，这里不能输入哦" disabled={true}></IcTextarea>
                </section>
                <section>
                    <IcTextarea  placeholder="小姐姐，请在这里输入哦" width="300" height = "200"></IcTextarea>
                </section>
                <section>
                    <IcTextarea placeholder="小哥哥，这里只能输入500个字符哦"  width={300} height={200} maxLength = "500"></IcTextarea>
                </section>
                <section>
                    <IcTextarea maxLength = "500" onChange = {this.handleValue}></IcTextarea>
                </section>
                <section>
                    <Markdown>{textareaMd}</Markdown>
                </section>
            </Fragment>
        );
    }
}



