import React, {Fragment} from 'react'
import IcLoading from "@eui/ic-loading";
import normal from './normal.md'
import text from './text.md'
import global from './global.md'
import delay from './delay.md'
import children from './children.md'
import Button from '@eui/ic-button'
import MarkDown from 'ic-markdown'

import './index.scss'
class Loading extends React.PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            loading: true
        }
    }
    componentWillUnmount() {
        if (this.timmer) {
            clearTimeout(this.timmer)
        }
        if (this.timmer1) {
            clearTimeout(this.timmer1)
        }
    }
    componentDidMount() {
        this.timer = setTimeout(() => {
            this.setState({
                loading: false
            })
        }, 2000);
    }
    onClick =()=> {
        this.setState({
            show: true
        })
        this.timmer1 = setTimeout(() => {
            this.setState({
                show: false
            })
        }, 2000);
    }
    render() {
        return (<Fragment>
            <section className='mb20'>
                <div>普通loading</div>
                <section><IcLoading/></section>
                <section><MarkDown>{normal}</MarkDown></section>
            </section>
            <section className='mb20'>
                <div>带辅助文字loading</div>
                <section><IcLoading text='加载中...' /></section>
                <section><MarkDown>{text}</MarkDown></section>
            </section>
            <section className='mb20'>
                <div>delay属性的Loading</div>
                <section>{this.state.show ? null : <IcLoading delay={2000} text='加载中...' />}</section>
                <section><MarkDown>{delay}</MarkDown></section>
            </section>
            <section className='mb20'>
                <div className='mb20'>带children的Loading</div>
                <section>
                    <IcLoading loading={this.state.loading}>
                        <div style={{color: '#FF8833', marginBottom: '20px'}}>我是Ic-Loading里面的内容</div>
                    </IcLoading>
                </section>
                <section><MarkDown>{children}</MarkDown></section>
            </section>
            <section className='mb20'>
                <div>全局loading</div>
                <section style={{textAlign: 'center'}}>
                    <Button onClick={this.onClick}>弹出全局loading</Button>
                </section>
                {this.state.show ? <IcLoading type='global' text='加载中...' /> : null}
                <section><MarkDown>{global}</MarkDown></section>
            </section>
        </Fragment>)
    }
}


export default Loading



