import React, { PureComponent,Fragment} from 'react'
import IcInput from '@eui/ic-input'
import IcIcon from '@eui/ic-iconfont'
import Markdown from 'ic-markdown'
import largeInput from './large.md'
import defaultInput from './default.md'
import smallInput from './small.md'
import numberInput from './number.md'

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section className="single-icon">large输入框</section>
                <section>
                    <IcInput size="large" placeholder = "请输入内容" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" size="large"></IcInput>
                </section>
                <section>
                    <IcInput size="large" placeholder = "请输入内容" width={300} endAdornment={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" size="large" width="400"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {largeInput}
                    </Markdown>
                </section>
                <section>default输入框</section>
                <section>
                    <IcInput placeholder = "请输入内容" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容"></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" width="300" endAdornment={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" width="400px"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {defaultInput}
                    </Markdown>
                </section>
                <section>small输入框</section>
                <section>
                    <IcInput placeholder = "请输入内容" size="small" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" size="small"></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" size="small" width="300px" endAdornment={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput placeholder = "请输入内容" size="small" width="400"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {smallInput}
                    </Markdown>
                </section>
                <section>数字输入框</section>
                <section><IcInput type = 'number'></IcInput></section>
                <section><IcInput width="200" type = 'number'></IcInput></section>
                <section><IcInput width="400" type = 'number' max = '100' min = '0'></IcInput></section>
                <section>
                    <Markdown>
                        {numberInput}
                    </Markdown>
                </section>
            </Fragment>
        );
    }
}



