import React, { Component } from 'react';
import Radio from '@eui/ic-radio';
import Markdown from 'ic-markdown';
import easyMD from './easy.md';
import disabledMD from './disabled.md';
import RadioGroup from './RadioGroup';
import radioGroupMD from './radio-group.md';
import GroupOptions from './GroupOptions';
import optionsMD from './groupOptions.md';
import GroupName from './GroupName';
import groupNameMD from './groupName.md';
import GroupSimple from './GroupSimple';
import groupSimpleMD from './groupSimple.md';

class App extends Component {
    render() {
        return (
            <section className='section-ic-radio'>
                <section>
                    <p>最简单的用法</p>
                    <Radio>easy</Radio>
                </section>
                <section>
                    <Markdown>{easyMD}</Markdown>
                </section>
                <section>
                    <p>禁用</p>
                    <Radio value='未选中禁用' defaultChecked={false} disabled>未选中禁用</Radio> <br/>
                    <Radio value='选中禁用' defaultChecked={true} disabled>选中禁用</Radio>
                </section>
                <Markdown>{disabledMD}</Markdown>
                <section>
                    <p>单选组合</p>
                    <RadioGroup />
                </section>
                <section>
                    <Markdown>{radioGroupMD}</Markdown>
                </section>
                <section>
                    <p>单选组合--配置方式</p>
                    <GroupOptions />
                </section>
                <Markdown>{optionsMD}</Markdown>
                <section>
                    <p>单选组合--配合name使用</p>
                    <GroupName />
                </section>
                <Markdown>{groupNameMD}</Markdown>
                <section>
                    <p>单选组合--简单模式</p>
                    <GroupSimple />
                </section>
                <Markdown>{groupSimpleMD}</Markdown>
            </section>
        )
    }
}

export default App



