import React, { PureComponent,Fragment} from 'react'
import IcBreadcrumb from '@eui/ic-breadcrumb'
import Markdown from 'ic-markdown'
import normalUse from './normal.md'
import childUse from './child.md'
import childUseSmall from './childSmall.md'
import historyUse from './history.md'

const routes = [{path: '/index', breadcrumbName: '首页'}, 
                {path: 'first', breadcrumbName: '列表'}, 
                {path: 'second', breadcrumbName: ':place'}]
const params = {
  place: '被替换的名字'
}

export default class IcBreadcrumbExample extends PureComponent{
    render() {
        return (
            <Fragment>
                <section>默认用法</section>
                <section>
                    <IcBreadcrumb routes={routes} params={params}/>
                </section>
                <section>
                    <Markdown>{normalUse}</Markdown>
                </section>
                <section>children用法</section>
                <section>
                <IcBreadcrumb>
                    <IcBreadcrumb.Item href='#/first'>一级</IcBreadcrumb.Item>
                    <IcBreadcrumb.Item><a href="#/first/second"> 二级</a></IcBreadcrumb.Item>
                    <IcBreadcrumb.Item><a href="#/first/second/third">三级</a></IcBreadcrumb.Item>
                    <IcBreadcrumb.Item>四级</IcBreadcrumb.Item>
                </IcBreadcrumb>
                </section>
                <section>
                    <Markdown>{childUse}</Markdown>
                </section>
                <section>次级导航</section>
                <section>
                <IcBreadcrumb size='small'>
                    <IcBreadcrumb.Item href='#/first'>一级</IcBreadcrumb.Item>
                    <IcBreadcrumb.Item><a href="#/first/second"> 二级</a></IcBreadcrumb.Item>
                    <IcBreadcrumb.Item><a href="#/first/second/third">三级</a></IcBreadcrumb.Item>
                    <IcBreadcrumb.Item>四级</IcBreadcrumb.Item>
                </IcBreadcrumb>
                </section>
                <section>
                    <Markdown>{childUseSmall}</Markdown>
                </section>
                <section>
                    <Markdown>{historyUse}</Markdown>
                </section>
            </Fragment>
        )
    }
}


