import React, { PureComponent,Fragment} from 'react'
import IcTagInput from '@eui/ic-tag-input'
import Markdown from 'ic-markdown'
import tagMd from './tagInput.md'

export default class extends PureComponent {
    handleData = (data) =>{
        console.log(data)
    }
    render() {
        return (
            <Fragment>
                <section>标签输入框</section>
                <section>
                    <section><IcTagInput placeholder = "请输入标签" width = {300} maxHeight={100} maxLength = {20}></IcTagInput></section>
                    <section><IcTagInput placeholder = "请输入标签" width = {300} maxHeight={100} onChange = {this.handleData}></IcTagInput></section>
                    <section><IcTagInput tagList = {["e小宝",'名校','高薪期权']} placeholder = "请输入标签" value = "地铁近"></IcTagInput></section>
                </section>
                <section>
                    <Markdown>{tagMd}</Markdown>
                </section>
            </Fragment>
        );
    }
}



