import React, {Fragment, Component} from 'react';
import IcPagination from '@eui/ic-pagination'
import MarkDown from 'ic-markdown'
import normal from './normal.md'
import './index.scss'
class Pagination extends Component {
    handleChange = (page, pageSize) => {
        console.log(page, pageSize)
    }
    render() {
        return (
        <Fragment>
            <section><IcPagination total={999} onChange={this.handleChange} /></section>
            <section>
                <MarkDown>{normal}</MarkDown>
            </section>
        </Fragment>
    )
    }
}

export default Pagination



