import React, { PureComponent,Fragment} from 'react'
import IcIcon from '@eui/ic-iconfont'
import Markdown from 'ic-markdown'
import singleIcon from './singleIcon.md'
import colorIcon from './colorIcon.md'
import './style.css'

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section className="single-icon">单色图标</section>
                <section>
                    <IcIcon type="close_small" className="fix-space" size={14}></IcIcon>
                    <IcIcon type="accumulate" className="fix-space" size={14}></IcIcon>
                    <IcIcon type="group_selected" className="fix-space" size={14}></IcIcon>
                    <IcIcon type="filter" className="fix-space" size={14}></IcIcon>
                    <IcIcon type="employ" className="fix-space" size={14}></IcIcon>
                    <IcIcon type="download" className="fix-space" size={14}></IcIcon>
                    <IcIcon type="close_small" className="fix-space" size='14px'></IcIcon>)
                </section>
                <section>
                    <Markdown>
                        {singleIcon}
                    </Markdown>
                </section>
                <section>彩色图标</section>
                <section>
                    <IcIcon type="nav_map" className="fix-space" isColor={true} size={18}></IcIcon>
                    <IcIcon type="big_data" className="fix-space" isColor={true} size={18}></IcIcon>
                    <IcIcon type="neitui" className="fix-space" isColor={true} size={18}></IcIcon>
                    <IcIcon type="wanke" className="fix-space" isColor={true} size={18}></IcIcon>
                    <IcIcon type="wanke-shangjin" className="fix-space" isColor={true} size={18}></IcIcon>
                    <IcIcon type="nav_service" className="fix-space" isColor={true} size={18}></IcIcon>
                    <IcIcon type="nav_service" className="fix-space" isColor={true} size="14px"></IcIcon>
                </section>
                <section>
                    <Markdown>
                        {colorIcon}
                    </Markdown>
                </section>
            </Fragment>
        );
    }　
}



