import React, {PureComponent} from 'react';
import Tabs from '@eui/ic-tabs';
import Markdown from 'ic-markdown';
import defaultMD from './default.md';
import smallMD from './small.md';
import controlledMD from './controlled.md';
import extraMD from './extra.md';
import './index.scss';

const TabPane = Tabs.TabPane;

function onChange(activeValue) {
    console.log(`changed tab to ${activeValue}`);
}

export default class extends PureComponent {
    state = {
        activeValue: 'aa'
    };
    changeTabs = (e) => {
        this.setState({
            activeValue: e.target.value
        });
    };
    render() {
        return (
            <div className="eui-tabs-examples">
                <section>选项卡切换</section>
                <section>
                    <p>默认选中第二个面板</p>
                    <Tabs defaultActiveValue="2" onChange={onChange}>
                        <TabPane label="Tab 1" value="1">Content of Tab 1</TabPane>
                        <TabPane label="Tab 2" value="2">Content of Tab 2</TabPane>
                        <TabPane label="Tab 3" value="3">Content of Tab 3</TabPane>
                    </Tabs>
                </section>
                <section>
                    <Markdown>{defaultMD}</Markdown>
                </section>
                <section>
                    <p>二级选项卡（未设置defaultActiveValue属时，默认选中第一个面板）</p>
                    <Tabs size="small" onChange={onChange}>
                        <TabPane label="Tab 1" value="1">Content of Tab 1</TabPane>
                        <TabPane label="Tab 2" value="2">Content of Tab 2</TabPane>
                        <TabPane label="Tab 3" value="3">Content of Tab 3</TabPane>
                    </Tabs>
                </section>
                <section>
                    <Markdown>{smallMD}</Markdown>
                </section>
                <section>
                    <p>受控</p>
                    <p>
                        <label>activeValue： </label>
                        <select name="" id="" onChange={(e) => this.changeTabs(e)}>
                            <option value="aa">value: aa</option>
                            <option value="bb">value: bb</option>
                            <option value="cc">value: cc</option>
                        </select>
                    </p>
                    <Tabs activeValue={this.state.activeValue}>
                        <TabPane label="Tab 1" value="aa">Content of Tab 1</TabPane>
                        <TabPane label="Tab 2" value="bb">Content of Tab 2</TabPane>
                        <TabPane label="Tab 3" value="cc">Content of Tab 3</TabPane>
                    </Tabs>
                </section>
                <section>
                    <Markdown>{controlledMD}</Markdown>
                </section>
                <section>
                    <p>选项右侧添加附加操作</p>
                    <Tabs tabBarExtraContent={<button>正常按钮</button>} defaultActiveValue="2" onChange={onChange}>
                        <TabPane label="Tab 1" value="1">Content of Tab 1</TabPane>
                        <TabPane label="Tab 2" value="2">Content of Tab 2</TabPane>
                        <TabPane label="Tab 3" value="3">Content of Tab 3</TabPane>
                    </Tabs>
                </section>
                <section>
                    <Markdown>{extraMD}</Markdown>
                </section>
            </div>
        );
    }
}




