import React, {PureComponent} from 'react';
import Tooltip from '@eui/ic-tooltip';
import Markdown from 'ic-markdown';
import './index.scss';
import easyMD from './easy.md';
import setCenterMD from './setCenter.md';
import directionMD from './direction.md';
import smallMD from './small.md';
import overflowMD from './overflow.md';

export default class extends PureComponent {
    render() {
        return (
            <div className="tooltip-example">
                <section>气泡提示</section>
                <section className="tooltip">
                    <p>最简单的用法</p>
                    <Tooltip title="这是最简单的用法">
                        <span>最简单的用法</span>
                    </Tooltip>
                </section>
                <section>
                    <Markdown>{easyMD}</Markdown>
                </section>

                <section className="tooltip">
                    <p>设置箭头指向目标元素中心</p>
                    <Tooltip title="tooltip text" placement="topLeft" arrowPointAtCenter>
                        <span>设置箭头指向目标元素中心</span>
                    </Tooltip>
                </section>
                <section>
                    <Markdown>{setCenterMD}</Markdown>
                </section>

                <section className="tooltip direction-tooltip">
                    <p>位置有12个方向</p>
                    <div className="direction-tooltip-top">
                        <Tooltip title="tooltip text" placement="topLeft">
                            <span>topLeft</span>
                        </Tooltip>
                        <Tooltip title="tooltip text" placement="top">
                            <span>top</span>
                        </Tooltip>
                        <Tooltip title="tooltip text" placement="topRight">
                            <span>topRight</span>
                        </Tooltip>
                    </div>
                    <div className="direction-tooltip-middle">
                        <div className="direction-tooltip-middle-left">
                            <Tooltip title="tooltip text" placement="leftTop">
                                <span>leftTop</span>
                            </Tooltip>
                            <Tooltip title="tooltip text" placement="left">
                                <span>left</span>
                            </Tooltip>
                            <Tooltip title="tooltip text" placement="leftBottom">
                                <span>leftBottom</span>
                            </Tooltip>
                        </div>
                        <div className="direction-tooltip-middle-right">
                            <Tooltip title="tooltip text" placement="rightTop">
                                <span>rightTop</span>
                            </Tooltip>
                            <Tooltip title="tooltip text" placement="right">
                                <span>right</span>
                            </Tooltip>
                            <Tooltip title="tooltip text" placement="rightBottom">
                                <span>rightBottom</span>
                            </Tooltip>
                        </div>
                    </div>
                    <div className="direction-tooltip-bottom">
                        <Tooltip title="tooltip text" placement="bottomLeft">
                            <span>bottomLeft</span>
                        </Tooltip>
                        <Tooltip title="tooltip text" placement="bottom">
                            <span>bottom</span>
                        </Tooltip>
                        <Tooltip title="tooltip text" placement="bottomRight">
                            <span>bottomRight</span>
                        </Tooltip>
                    </div>

                </section>
                <section>
                    <Markdown>{directionMD}</Markdown>
                </section>

                <section className="tooltip small-tooltip">
                    <p>目标元素的高度小于48像素</p>
                    <Tooltip title={<div>tooltip text</div>} placement="rightTop">
                        <span>目标元素的高度小于48像素</span>
                    </Tooltip>
                </section>
                <section>
                    <Markdown>{smallMD}</Markdown>
                </section>

                <section className="tooltip overflow-tooltip">
                    <p>气泡被遮挡时不调整气泡位置</p>
                    <Tooltip title={<div>我是很大的内容区域,placement设置成left只是为了让气泡出现在页面左边时使气泡被遮挡</div>} autoAdjustOverflow={false} placement="left">
                        <span>气泡被遮挡时不调整气泡位置</span>
                    </Tooltip>
                </section>
                <section>
                    <Markdown>{overflowMD}</Markdown>
                </section>
            </div>
        );
    }
}




