import React, {PureComponent} from 'react'
import Form from 'ic-form'
import {Input, Textarea, Switch, RadioGroup,Checkbox} from '@eui/ic-field-decorator'
import Radio from '@eui/ic-radio'
import SubmitButton from '@eui/ic-submit-button'

export default class extends PureComponent {
    handlerSubmit = (data) => {
        console.log("提交表单", data);
    };

    render() {
        return (
            <Form onSubmit={this.handlerSubmit}>
                <Input name="name" label="用户名" rule="REQ NAME LEN-2-10"/>
                <Input type="password" name="pwd" label="密码" rule="REQ LEN-6-12"/>
                <Textarea name="des" label="个人介绍" rule="REQ"/>
                <Switch name="sex" label="性别"/>
                <RadioGroup name="type" label="类型">
                    <Radio value={0}>学生</Radio>
                    <Radio value={1}>老师</Radio>
                </RadioGroup>
                <SubmitButton>保存</SubmitButton>
            </Form>
        );
    }
}

export class CustomRules extends PureComponent {
    handlerSubmit = (data) => {
        console.log("提交表单", data);
    };

    render() {
        return (
            <Form onSubmit={this.handlerSubmit} rules={{
                NUM: (value) => {
                    return {
                        result: !Number.isNaN(Number(value)),
                        errMsg: '%s必须是数字'
                    };
                },
                IS_EXIST: (value) => {
                    return new Promise((resolve) => {
                        const phoneSet = ['18000000000', '18000000001', '18000000002'];
                        setTimeout(() => resolve({
                            result: value && phoneSet.indexOf(value) > -1,
                            errMsg: '%s不存在'
                        }), 1000);
                    });
                }
            }}>
                <Input name="name" label="用户名" rule="REQ NAME LEN-2-10"/>
                <Input type="password" name="pwd" label="密码" rule="REQ NUM LEN-6-12"/>
                <Input name="tel" label="电话" rule="REQ TEL IS_EXIST"/>
                <SubmitButton>保存</SubmitButton>
            </Form>
        );
    }
}



