import React, { Component } from 'react';
import Checkbox from '@eui/ic-checkbox';
import Markdown from 'ic-markdown';
import Easy from './Easy';
import easyMD from './easy.md';
import disabledMD from './disabled.md';
import GroupOptions from './GroupOptions';
import optionsMD from './options.md';
import groupThemeMD from './groupTheme.md';

const CheckboxGroup = Checkbox.Group;

class App extends Component {
    render() {
        return (
            <section className='section-ic-checkbox'>
                <section>
                    <p>最简单的用法</p>
                    <Easy />
                </section>
                <section>
                    <Markdown>{easyMD}</Markdown>
                </section>
                <section>
                    <p>禁用</p>
                    <Checkbox defaultChecked={false} disabled /><Checkbox defaultChecked disabled />
                </section>
                <Markdown>{disabledMD}</Markdown>
                <section>
                    <p>多选组合--配置方式</p>
                    <GroupOptions />
                </section>
                <Markdown>{optionsMD}</Markdown>
                <section>
                    <p>复选组合--主题</p>
                    <Checkbox theme="blue">blue</Checkbox>
                    <br/>
                    <CheckboxGroup options={['Apple', 'Pear', 'Orange']} defaultValue={['Apple']} theme="blue" />
                </section>
                <Markdown>{groupThemeMD}</Markdown>
            </section>
        )
    }
}

export default App



